import React from 'react'
const { Octokit } = require('@octokit/rest')

const octokit = new Octokit({
  auth: '501d530a159fc4fb7f946263b91fc509a11c6d85'
})

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      items: []
    }
  }

  componentDidMount() {
    this.octokit = octokit
    this.getData()
  }

  async getData() {
    const res = await this.octokit.repos.getContent({
      owner: 'ryosuzuki',
      repo: 'monitor-db',
      path: 'db.json',
      headers: {
        'If-None-Match': ''
      }
    })
    try {
      const items = JSON.parse(atob(res.data.content))
      console.log(items)
      const posts = this.items.filter((item => item.type == 'post'))
      const downloads = this.items.filter((item => item.type !== 'post'))
      this.setState({ items: items, posts: posts, downloads: downloads })
    } catch (err) {
      console.log(err)
    }
  }

  render() {
    return (
      <div>
        <title>Monitor</title>
        <div class="ui two column centered grid">
          <div class="column">
            <h1>Monitor</h1>

            <h1>Post</h1>
            <table class="ui celled table">
              <thead>
                <tr><th>Name</th>
                <th>Age</th>
                <th>Job</th>
              </tr></thead>
              <tbody>
                { this.state.posts.map((post) => {
                  return (
                    <tr>
                      <td data-label="Name">{ post.timestamp }</td>
                      <td data-label="Age">{ post.error }</td>
                      <td data-label="Job">{ post.blog }</td>
                    </tr>
                  )
                })}
              </tbody>
            </table>

          </div>
        </div>
      </div>
    )
  }
}

export default Index